$clr-1: rgb(4, 10, 26);
$clr-1: rgb(65, 109, 255);
$clr-2: rgb(112, 19, 34);
$clr-2-2: rgb(133, 10, 84);
$clr-3: rgb(254, 255, 235);
$clr-4: rgb(201, 65, 87);
$clr-black: rgb(20, 20, 20);

// @import url('https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;500&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Zen+Old+Mincho:wght@400;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    // font-family: 'EB Garamond', serif;
    font-family: 'Zen Old Mincho', serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: hsl(0, 0%, 10%);
    min-height: 100vh;
    color: hsl(0, 0%, 10%);
    position: relative;
    overflow-x: hidden;
}

h1 {
    font-size: clamp(0.75rem, 1.5rem + 2vw, 3rem);
    letter-spacing: 2px;
    font-weight: 600;
    text-align: center;
}

span {
    color: white;
    font-size: clamp(0.75rem, 1.5rem + 2vw, 3rem);
}

.presents img {
    display: block;
    margin: 6rem 0;
}

.presents {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.app {
    opacity: 0;
}

.app.visible {
    transition: opacity 3s linear;
    opacity: 1;
}

.down {
    position: fixed;
    bottom: 5%;
    right: 5%;
    border-radius: 25%;
    border-top: 20px solid rgba(253, 112, 190, 0.5);
    border-left: 10px solid rgba(0, 0, 0, 0);
    border-right: 10px solid rgba(0, 0, 0, 0);
    z-index: 200;
    animation: blink 0.5s linear infinite alternate;
}

@keyframes blink {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.scroll-down {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    color: hsl(0, 0%, 96%);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.arrow {
    width: 25px;
    height: 25px;
    border-left: 5px solid #fff;
    border-bottom: 5px solid #fff;
    transform: rotate(-45deg);
    border-radius: 5px;
    animation: arrow 0.5s ease-in-out infinite alternate;
}

@keyframes arrow {
    0% {
        transform: rotate(-60deg);
    }
    100% {
        transform: rotate(-15deg);
    }
}

.window {
    position: relative;
    width: 100vw;
    color: #fff;
    overflow: hidden;
    z-index: -1;
}

.message-wrapper {
    position: relative;
    overflow-x: hidden;
    z-index: 10;
    top: 50vh;
    width: 100vw;
    height: 100vh;
}

.message-box {
    position: relative;
    width: 100vw;
    // height: 50vh;
    text-align: center;
    margin: 3rem 0;
}

.night {
    position: relative;
    width: 100vw;
    height: 200vh;
}

.stars-box {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    overflow: hidden;
}

.star {
    position: absolute;
    border-radius: 50%;
    background: rgb(0, 0, 0);
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 1.1);
}

.moon-box {
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
    width: 100vw;
    height: 70px;
    overflow-x: hidden;
}

#moon {
    width: 70px;
    height: 70px;
}

.moon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-shadow: -20px -10px 0px rgb(0, 0, 0);
    filter: drop-shadow(0px 0px 10px rgb(0, 0, 0));
    z-index: 10;
}

.day {
    position: relative;
    width: 100vw;
    height: 200vh;
}

.sun-wrapper {
    width: 100vw;
    height: 200px;
    overflow-x: hidden;
}

.sun-box {
    position: relative;
    display: flex;
    align-items: center;
    width: 100vw;
}

.sun {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    box-shadow: 0px 0px 20px rgb(255, 255, 170);
    background: rgb(255, 255, 170);
}

.cloud-box {
    position: relative;
    overflow-x: hidden;
    width: 100vw;
    // height: 30vh;
    z-index: 10;
}

.hb-message {
    position: absolute;
    top: 20%;
    width: 100vw;
    white-space: pre;
    display: flex;
    // flex-wrap: wrap;
    justify-content: center;
    color: white;
    font-size: clamp(0.65rem, 1.75rem + 3vw, 3rem);
    text-align: center;
}

.baloon {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    animation: baloon 1s ease-in infinite alternate;
}

@keyframes baloon {
    0% {
        transform: translate3d(-50%, 50px, 0);
    }
    100% {
        transform: translate3d(-50%, 0px, 0);
    }
}